* {
  box-sizing: border-box;
}

body {
  margin: 0 50px;
  font-family:Arial, Helvetica, sans-serif
}

.note {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 13px;
  font-size: 18px;
  color: rgb(51, 51, 51);
}

.col {
  min-width: 300px;
}

.error {
  margin: 0 10px 10px;
  padding: 10px;
  border-style: solid;
  border-radius: 5px;
  background: rgb(255, 215, 215);
  color: red;
  font-size: 20px;
}
